.catalog__title {
  width: 81%;
  margin: 0 auto;
  font-size: 27px;
  line-height: 30px;
  font-weight: bold;
  display: flex;
  justify-content: center;
  padding-top: 71px;
  padding-bottom: 76px;
  text-align: center;
}

.catalog__wrapper-title {
  background-color: #f1f1f1;
}

.catalog__list {
  list-style: none;
  margin: 0 auto;
  padding: 0;
  display: flex;
  flex-direction: column;
  padding-top: 31px;
  width: 81%;
}

.catalog__wrapper-description {
  order: 1;
  border: 1px solid #ededed;
  padding-top: 25px;
  padding-left: 27px;
  padding-right: 27px;
  box-sizing: border-box;
}

.catalog__wrapper-basket-price {
  border-top: 1px solid #ececec;
  display: flex;
}

.catalog__link {
  color: #000000;
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
  display: flex;
  max-width: 200px;

  &:hover {
    color: rgba(#000000, 0.6);
  }

  &:active {
    color: rgba(#000000, 0.3);
  }
}

.catalog__title-item {
  margin: 0;
  font-size: 17px;
  line-height: 24px;

  &:active {
    color: rgba(#000000, 0.3);
  }
}

.catalog__description {
  margin: 0;
  font-size: 13px;
  line-height: 30px;
  font-weight: normal;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 14px;
}

.catalog__price {
  margin: 0;
  font-size: 15px;
  line-height: 24px;
  font-weight: bold;
  padding-top: 21px;
  padding-bottom: 27px;
  position: relative;
  width: 180px;
  margin-right: auto;
}

.catalog__basket {
  width: 20px;
  height: 19px;
  display: flex;
  margin-top: 23px;
}

.catalog__basket .catalog__basket-icon {
  fill: #cccccc;
}

.catalog__basket:hover .catalog__basket-icon {
  fill: #63d1bb;
}

.catalog__basket:active {
  opacity: 0.3;
}

.catalog__image {
  margin: 0;
  display: flex;
}

.catalog__picture {
  width: 100%;
  height: 100%;
  display: flex;
}

.catalog__image img {
  width: 100%;
  height: 100%;
}

.catalog__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 31px;
  transition: box-shadow 0.3s ease-in-out;
}

.catalog__item:hover {
  box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.1);
}

.catalog__item:active {
  box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
}

@media (min-width: 768px) {
  .catalog__title {
    font-size: 43px;
    line-height: 50px;
    padding-top: 36px;
    padding-bottom: 80px;
    margin-bottom: 20px;
    width: 87%;
  }

  .catalog__wrapper-title {
    position: relative;
    background-color: #f1f1f1;
  }

  .catalog__description {
    font-size: 15px;
    line-height: 30px;
    margin-bottom: auto;
  }

  .catalog__price {
    font-size: 17px;
    line-height: 24px;
    padding-left: 4px;
  }

  .catalog__basket {
    margin-right: 5px;
  }

  .catalog__wrapper-title::after {
    position: absolute;
    content: "";
    background-image: url("../img/bg-zigzag-fill.svg");
    background-repeat: repeat-x;
    background-position: 0 bottom;
    width: 100%;
    height: 35px;
    bottom: -12px;
    left: 0;
    background-size: 768px;
  }

  .catalog__item {
    flex-direction: row;
    margin-bottom: 54px;
  }

  .catalog__list {
    width: 87%;
    padding-top: 59px;
  }

  .catalog__link {
    max-width: 240px;
  }

  .catalog__title-item {
    font-size: 20px;
    line-height: 24px;
  }

  .catalog__image img {
    width: 100%;
    height: 100%;
  }

  .catalog__wrapper-description {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 50%;
  }

  .catalog__image {
    width: 50%;
  }
}

@media (min-width: 1150px) {
  .catalog__list {
    flex-wrap: wrap;
    width: 1050px;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 116px;
  }

  .catalog__description {
    width: 250px;
  }

  .catalog__title {
    font-size: 53px;
    line-height: 57px;
    padding-top: 48px;
    padding-bottom: 115px;
    margin-bottom: 0;
    width: 1050px;
  }

  .catalog__item {
    flex-direction: column;
    min-height: 660px;
  }

  .catalog__image {
    width: auto;
  }

  .catalog__wrapper-description {
    width: auto;
  }

  .catalog__image img {
    width: 316px;
    height: 462px;
  }

  .catalog__wrapper-title::after {
    bottom: -4px;
    background-size: 1150px;
  }

  .catalog__price {
    padding-left: 0;
  }

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .catalog__description {
      padding-bottom: 39px;
    }

    .catalog__item:nth-child(3) .catalog__description {
      padding-bottom: 15px;
    }
  }
}
